:root {
  --border-size: 2px;
  --default-color: #ddd;
}

.App {
  text-align: center;
  padding: 0;
  margin: 0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1 {
  font-size:3em;
}
button,
a.button {
  border: 2px solid;
  border-color: #aaa black black #aaa;
  color: black;
  text-decoration: none;
}
a.button,
button {
  cursor: pointer;
}

label {
  margin-right: 1em;
  font-weight: bold;
}

#main-menu {
  display: inline-block;
  border: 1px solid black;
  margin: 2em auto;
  padding: 2em 4em;
}
#main-menu h1 {
  text-decoration: underline;
}

#settings button,
#main-menu a.button,
#main-menu button {
  background-color: #ddd;
  padding: 1em;
  font-size: 2em;
  display: block;
  margin: 2em auto;
}
#main-menu a.button {
  display: inline-block;
  margin: 0 auto;
}

.canvasButtons {
  position: absolute;
  top: 0;
  left: 0;
}

#settings {
  display: inline-block;
  margin: 0 auto;
}

.setting {
  text-align: left;
  margin-bottom: 1em;
}
.setting label {
  font-size: 1.5em;
}
.setting .help {
  font-style: italic;
  font-color: #ddd;
}
#default-color-sample {
  background-color: var(--default-color);
}
.colorsample {
  min-width: 3em;
  border: 1px solid black;
  display: inline-block;
  min-height: 1em;
  vertical-align: middle;
  margin-left: 1em;
}
.colorsample.gradient {
  min-width: 1em;
  margin-left: 0;
}

#gradient-options,
#palette-options {
  margin-left: 1em;
}
#palette-type-options label,
#palette-options label {
  font-size: 1em;
}
#palette-type-options input,
#palette-options input {
  margin-right: 0.5em;
}
.custom-palette-creator-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ddd;
  opacity: 0.5;
  z-index: 1000;
}
.custom-palette-creator-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 90vw;
  height: 90vh;
  margin: 5vh 5vw;
  background-color: #fff;
  z-index: 1001;
  text-align: center;
}
.custom-palette-creator {
  display: inline-block;
  margin: 1em auto;
  padding: 0.5em;
  max-width: 90vw;
}
.custom-palette-creator th,
.custom-palette-creator td {
  border-left: 1px dashed black;
  padding-left: 0.5em;
}
.custom-palette-creator th.first,
.custom-palette-creator td.first {
  border-left: none;
}
.custom-palette-creator input {
  width: 7em;
}
#settings button.edit,
#settings .custom-palette-creator button {
  display: inline-block;
  padding: 0em;
  font-size: 1em;
  margin: 0;
}
#settings button.edit {
  margin-left: 1em;
}
#settings .custom-palette-creator button.close {
  width: 2em;
  float: left;
  margin: 0.5em;
}
#settings .custom-palette-creator button.add-entry {
  width: 3em;
  margin: auto;
}
#settings .custom-palette-creator button.edit,
#settings .custom-palette-creator button.remove,
#settings .custom-palette-creator button.add-before {
  width: 2em;
  margin: 0;
}
#settings button.edit {
  width: 1.5em;
}
#settings .custom-palette-creator button.add-before img {
  transform: scaleX(-1);
}
#settings .customPalette {
  display: inline;
}
.customPalette span {
  margin-left: 1em;
}
.palette-entries {
}
.palette-entry {
  display: inline-block;
}

.btap {
  margin: calc(-1 * var(--border-size));
  border-width: var(--border-size);
  border-style: solid;
  border-color: black;
  display: grid;
  background-color: var(--default-color);
  cursor: crosshair;
}

.btap:hover {
  border-color: green;
}
.btap.base {
  height: 90vh;
  width: 90%;
  margin: 5vh auto;
  display: inline-grid;
  grid-template-columns: 1fr 1fr;
}
.btap.h {
  grid-template-rows: 1fr 1fr;
}
.btap.v {
  grid-template-columns: 1fr 1fr;
}